export const useSuspense = (f, ref, ...params) => {
    if (!ref.suspender) {
        ref.suspender = f(...params).then((result) => {
            ref.result = result;
        });
    }
    if (ref.error) {
        throw ref.error;
    }
    if (ref.result) {
        return ref.result;
    }
    throw ref.suspender;
};
